<template>
	<el-form class="y-module" ref="form" :model="form" label-width="80px" label-position="left">  
		<div v-if="editabled" class="y-title">
			<span class="y-right" :class="EditInfo ? 'y-hide' : 'y-show'"> 
				<el-button @click="EditInfo = true" type="primary" size="small">编辑</el-button> 
			</span>
			<span class="y-right" :class="EditInfo ? 'y-show' : 'y-hide'"> 
				<el-button @click="saveInfo" type="primary" size="small">保存</el-button>
				<el-button @click="EditInfo = false" type="primary" size="small">取消</el-button>
			</span>
			<i class="iconfont icon-mycenter text-yellow"></i>  
			基本信息 
		</div>
		<div v-else class="y-title"> 
			<i class="iconfont icon-mycenter text-yellow"></i>  
			基本信息 
		</div>
		<el-row :gutter="5">
			<el-col :xs="24" :sm="12" :md="8" :lg="6" v-for="item in listdata">
				<el-form-item :label="item.text"> 
					<el-input v-if="!item.edit" readonly="readonly" :value="item.val"></el-input>
					<el-input v-else :readonly="!EditInfo" v-model="form[item.id]"></el-input>
				</el-form-item>
			</el-col> 
		</el-row>  
	</el-form> 
</template>
<script> 
export default {  
	props: ['listdata', 'editabled'],
	data() {
		return {    
	        EditInfo: false
		}
	},  
	computed: { 
		// 计算属性
		form: function () { 
			var initdata = this.listdata;
			var _form = {};
			for(var i = 0, l = initdata.length; i < l; i++){
				var _d = initdata[i];
				var _id = _d.id;

				if( _d.edit ){
					_form[_id] = _d.val;
				};
			}; 
			return _form;
		}
	}, 
	methods: {  
		saveInfo(){
			this.$emit( 'save', this.form );
			this.EditInfo = false;
		} 
	}
}
</script> 


