<template>
	<div>
		<div class="y-breadcrumb-box">
			<el-breadcrumb separator="/"> 
			  <el-breadcrumb-item :to="{ path: '/Index' }">首页</el-breadcrumb-item>
			  <el-breadcrumb-item>人事管理</el-breadcrumb-item> 
			  <el-breadcrumb-item>我的信息</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="y-row-box">
			<div class="y-inner">  
				<!-- 基本信息 begin -->
				<BaseInfo :listdata="initdata" :editabled="true" @save="saveBaseInfo"></BaseInfo> 
				<!-- 基本信息 end -->
				<!-- 工作履历 begin -->
				<InfoList 
					title='<i class="iconfont icon-mycenter text-yellow"></i>工作履历'
					:listdata="jobListData" 
					:editabled="true"  
					:theaddata='jobTheadData'
					@add="ShowAddJob = true"
					@delete="deleteJobInfo"
					@save="saveJobInfo"></InfoList> 
				<!-- 工作履历 end -->
				<!-- 教育信息 begin -->
				<InfoList 
					title='<i class="iconfont icon-mycenter text-yellow"></i>教育信息'
					:listdata="eduListData" 
					:editabled="true"  
					:theaddata='eduTheadData'
					@add="ShowAddEdu = true"
					@delete="deleteEduInfo"
					@save="saveEduInfo"></InfoList> 
				<!-- 教育信息 end -->
				<!-- 培训记录 begin -->
				<InfoList 
					title='<i class="iconfont icon-mycenter text-yellow"></i>培训记录'
					:editabled="false"  
					:listdata="peixunListData"  
					:theaddata='peixunTheadData'></InfoList> 
				<!-- 培训记录 end -->
			</div> 
		</div>
		<AddJob :VisibleFlag="ShowAddJob" @close="ShowAddJob = false" @submit="addJobInfo"></AddJob>  
		<AddEdu :VisibleFlag="ShowAddEdu" @close="ShowAddEdu = false" @submit="addEduInfo"></AddEdu>  
	</div>
</template>
<script>
import {formatDate} from '@/assets/js/date.js';
import BaseInfo from './components/baseInfo';
import InfoList from './components/infoList';

import AddJob from './components/addJob'; //添加工作经历
import AddEdu from './components/addEdu'; //添加教育经历

export default {
	data() {
		return {   
	        //显示添加工作经历弹窗
	        ShowAddJob: false,
	        //显示添加教育经历弹窗
	        ShowAddEdu: false,
			initdata: [{
				id: 'name',
				text: '姓名',
				val: '张三',
				edit: false
			},{
				id: 'sex',
				text: '性别',
				val: '男',
				edit: false
			},{
				id: 'date',
				text: '出生日期',
				val: '1990-6-18',
				edit: false
			},{
				id: 'mail',
				text: '电子邮箱',
				val: 'zhangsan@yonyou.com',
				edit: true
			},{
				id: 'phone',
				text: '手机',
				val: '187xxxxxxxx',
				edit: true
			}],   
	        jobListData: [{
				date: '2016-05-02',
				danwei: '单位1',
				zhiwu: '经理1',
				zhize: 'aa'
			}],
			jobTheadData: [{
				param: 'date',
				text: '起止时间',
				required: true
			},{
				param: 'danwei',
				text: '工作单位',
				required: true
			},{
				param: 'zhiwu',
				text: '担任职务' 
			},{
				param: 'zhize',
				text: '主要职责' 
			}],
			eduListData: [{
				date: '2016-05-02',
				school: '单位1',
				zhuanye: '经理1',
				xueli: 'aa',
				xuewei: 'aa'
			}],
			eduTheadData: [{
				param: 'date',
				text: '起止时间',
				required: true
			},{
				param: 'school',
				text: '学校',
				required: true
			},{
				param: 'zhuanye',
				text: '专业',
				required: true 
			},{
				param: 'xueli',
				text: '学历',
				required: true 
			},{
				param: 'xuewei',
				text: '学位' 
			}],
			peixunListData: [{
				date: '2016-05-02',
				huodong: '单位1',
				xueshi: '经理1',
				danwei: 'aa',
				mubiao: 'aa',
				neirong: 'aa',
				chengji: 'aa'
			}],
			peixunTheadData: [{
				param: 'date',
				text: '起止时间' 
			},{
				param: 'huodong',
				text: '培训活动' 
			},{
				param: 'xueshi',
				text: '培训学时' 
			},{
				param: 'danwei',
				text: '培训单位' 
			},{
				param: 'mubiao',
				text: '培训目标' 
			},{
				param: 'neirong',
				text: '培训内容' 
			},{
				param: 'chengji',
				text: '培训成绩' 
			}]  
		}
	},
	components: {
		BaseInfo, //基本信息
		InfoList, //信息列表 
		AddJob, //添加工作履历弹窗
		AddEdu, //添加教育经历弹窗
	}, 
	methods: { 
		//基本信息-[保存]
		saveBaseInfo( data ){ 
			console.info( 'saveBaseInfo基本信息：', data);  
		},
		//工作履历-[动态添加]
		addJobInfo( data ){
			var _sdate = formatDate( data.date[0], 'yyyy-MM-dd');
			var _edate = formatDate(data.date[1], 'yyyy-MM-dd'); 
			data.date = _sdate + '至' + _edate;   

			this.ShowAddJob = false;   
			this.jobListData.push( JSON.parse(JSON.stringify( data )) ); 
		},
		//工作履历-[动态删除]
		deleteJobInfo(index, row) {  
			this.jobListData.splice(index, 1);
		},
		//工作履历-[保存,调用接口保存数据]
		saveJobInfo(){
			alert('保存')
		},
		//教育经历-[动态添加]
		addEduInfo( data ){
			var _sdate = formatDate( data.date[0], 'yyyy-MM-dd');
			var _edate = formatDate(data.date[1], 'yyyy-MM-dd'); 
			data.date = _sdate + '至' + _edate;   

			this.ShowAddEdu = false;   
			this.eduListData.push( JSON.parse(JSON.stringify( data )) ); 
		},
		//教育经历-[动态删除]
		deleteEduInfo(index, row) {  
			this.eduListData.splice(index, 1);
		},
		//教育经历-[保存,调用接口保存数据]
		saveEduInfo(){
			alert('保存')
		}  
	}
}
</script> 


