<template>
	<div class="y-module">
		<div v-if="editabled" class="y-title">
			<span class="y-right" :class="EditInfo ? 'y-hide' : 'y-show'"> 
				<el-button @click="EditInfo = true" type="primary" size="small">编辑</el-button> 
			</span>
			<span class="y-right" :class="EditInfo ? 'y-show' : 'y-hide'"> 
				<el-button @click="saveRow" type="primary" size="small">保存</el-button>
				<el-button @click="EditInfo = false" type="primary" size="small">取消</el-button>
			</span>
			<span v-html="title"></span>
		</div>
		<div v-else class="y-title"> 
			<span v-html="title"></span>
		</div>
		<el-table :data="listdata" style="width: 100%">
			<template v-for="item in theaddata">
				<el-table-column v-if="!item.required" :prop="item.param" :label="item.text"></el-table-column>
				<el-table-column v-else :render-header="requiredCol" :prop="item.param" :label="item.text"></el-table-column>
			</template> 
			<el-table-column v-if="editabled">
				<template scope="scope"> 
					<el-button
					size="small"
					type="danger"
					v-show="EditInfo"
					@click="deleteRow(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<div v-if="editabled" class="text-center" :class="EditInfo ? 'y-show' : 'y-hide'">
			<span class="text-blue" @click="addRow">添加</span>
		</div>
	</div> 
</template>
<script> 
export default {
	 //listdata: 列表数据[{}]; 
	 //editabled:是否可编辑 Boolean(true)
	 //title: 模块标题
	 //required: 加*的必填列 
	props: ['listdata', 'editabled', 'title', 'theaddata'],
	data() {
		return {   
			EditInfo: false 
		}
	},   
	methods: {  
		saveRow(){
			this.$emit('save');
			this.EditInfo = false;
		},
		addRow(){ 
			this.$emit('add');
		},
		deleteRow(index, row){
			this.$emit('delete',index, row);
		},
		//列表必填列
		requiredCol(createElement, { column }) {
			return [ 
				createElement('span', {
					'class': 'text-red'
				},'*'), column.label 
			];
		} 
	}
}
</script> 


